<template>
  <div class="customer-profile">
    <vue-user-info :user-info="getCustomerInfo" v-if="props.tabId === 1" />

    <div class="order-active__cards" v-if="props.tabId === 2">
      <vue-customer-active-card
        v-for="(card, index) of getCustomerInfo.activeOrder"
        :key="index"
        :card="card"
        v-if="getLengthActiveCards > 0"
      />
      <div class="order-active__warning" v-else-if="getLengthActiveCards === 0">
        <p class="order-active__warning--p">Активных заказов нет</p>
      </div>
    </div>

    <div class="order-done__cards" v-if="props.tabId === 3">
      <vue-customer-done-card
        v-for="(card, index) of getCustomerInfo.doneOrder"
        :key="index"
        :card="card"
        v-if="getLengthDoneCards > 0"
      />
      <div class="order-done__warning" v-else-if="getLengthDoneCards === 0">
        <p class="order-done__warning--p">Завершенных заказов нет</p>
      </div>
    </div>

    <div class="create-order" v-if="props.tabId === 4">
      <vue-create-order-form />
    </div>

    <div class="customer-template">
      <vue-customer-template v-if="props.tabId === 5" />
    </div>
  </div>
</template>
<script lang="ts" setup>
import VueUserInfo from "../Forms/VueUserInfo.vue";
import customerInfo from "@/localDatabase/customerInfo.json";
import { computed, defineProps } from "vue";
import CustomerInfo from "@/interfaces/CustomerInfo";
import VueCustomerActiveCard from "../Cards/VueCustomerActiveCard.vue";
import VueCustomerDoneCard from "../Cards/VueCustomerDoneCard.vue";
import VueCreateOrderForm from "../Forms/VueCreateOrderForm.vue";
import VueCustomerTemplate from "../Template/VueCustomerTemplate.vue";

const getCustomerInfo = computed((): CustomerInfo => {
  return customerInfo;
});

const getLengthDoneCards = computed((): number => {
  if (getCustomerInfo) {
    return getCustomerInfo.value!.doneOrder.length;
  } else return 0;
});

const getLengthActiveCards = computed((): number => {
  if (getCustomerInfo) {
    return getCustomerInfo.value!.activeOrder.length;
  } else return 0;
});

const props = defineProps({
  tabId: Number,
});
</script>
<style lang="scss">
.customer-profile {
  font-family: "sf-pro-400";
  font-size: 20px;
  width: 100%;
}
</style>
