<template>
  <div class="form-create-order">
    <vue-form>
      <div class="form__switcher">
        <label>Кто нужен?</label>
        <vue-switcher v-model="orderSwitcherParams" :disabled="disabledItems" />
      </div>
      <div class="first-option" v-if="orderSwitcherParams[0].isSwitch === true">
        <div class="form__location" v-if="!orderCheckBoxParams">
          <label>Адрес</label>
          <vue-text-input
            v-model="locationEnd"
            :placeholder="'Адрес'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__time">
          <label>В какое время нужно прибыть?</label>
          <vue-number-input
            v-model="inputTime.time.value"
            v-model:is-valid="inputTime.isValid.value"
            :type="'time'"
            :placeholder="'12:00'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__people">
          <label>Нужное количество грузчиков</label>
          <vue-number-input
            v-model="inputPeople.peopleCount.value"
            :placeholder="'Кол-во'"
            v-model:is-valid="inputPeople.isValid.value"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__description">
          <label>Описание проекта</label>
          <vue-text-area
            v-model="orderDescription.description.value"
            v-model:is-valid="orderDescription.isValid.value"
            :placeholder="'Что нужно сделать?'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__working-time">
          <label>Ориентировочное время занятости (в минутах)</label>
          <vue-number-input
            v-model="inputWorkingTime.time.value"
            v-model:is-valid="inputWorkingTime.isValid.value"
            :placeholder="'60'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__price">
          <label>Цена за заказ (руб)</label>
          <vue-number-input
            v-model="inputPrice.price.value"
            v-model:is-valid="inputPrice.isValid.value"
            :placeholder="'500'"
            :disabled="disabledItems"
          />
        </div>
      </div>

      <div
        class="second-option"
        v-if="orderSwitcherParams[0].isSwitch === false"
      >
        <div class="form__location-transportation__start">
          <label>Пункт "А"</label>
          <vue-text-input
            v-model="locationStart"
            :placeholder="'Адрес'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__location-transportation__end">
          <label>Пункт "Б"</label>
          <vue-text-input
            v-model="locationEnd"
            :placeholder="'Адрес'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__time">
          <label>В какое время нужно прибыть?</label>
          <vue-number-input
            v-model="inputTime.time.value"
            v-model:is-valid="inputTime.isValid.value"
            :type="'time'"
            :placeholder="'12:00'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__people">
          <label>Нужное количество грузчиков (если нет, то впишите 0)</label>
          <vue-number-input
            v-model="inputPeople.peopleCount.value"
            :placeholder="'Кол-во'"
            v-model:is-valid="inputPeople.isValid.value"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__description">
          <label>Что перевозить?</label>
          <vue-text-area
            v-model="orderDescription.description.value"
            v-model:is-valid="orderDescription.isValid.value"
            :placeholder="'Что нужно сделать?'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__price">
          <label>Цена за заказ (руб)</label>
          <vue-number-input
            v-model="inputPrice.price.value"
            v-model:is-valid="inputPrice.isValid.value"
            :placeholder="'500'"
            :disabled="disabledItems"
          />
        </div>
      </div>
      <div class="form__edit">
        <vue-yellow-button
          :title="stateBtnEdit.title"
          @click="startEditTemplate"
        />
        <vue-red-button
          :title="'Удалить'"
          @click="deleteTemplate(props.template.id)"
        />
      </div>
    </vue-form>
  </div>
</template>

<script setup lang="ts">
import VueForm from "@/components/Forms/VueForm.vue";
import VueTextInput from "@/components/Inputs/VueTextInput.vue";
import VueTextArea from "@/components/Inputs/VueTextArea.vue";
import VueSwitcher from "@/components/Switchers/VueSwitcher.vue";
import VueNumberInput from "@/components/Inputs/VueNumberInput.vue";
import VueYellowButton from "@/components/Buttons/VueYellowButton.vue";
import VueRedButton from "../Buttons/VueRedButton.vue";

import { ref, defineProps, defineEmits, computed } from "vue";
import { Template } from "@/interfaces/Template";
import { userTemplatesStore } from "@/store/template";
import { toast } from "vue3-toastify";

const templateStore = userTemplatesStore();

const props = defineProps({
  template: {
    type: Object as () => Template,
    required: true,
  },
  disabled: {
    type: Boolean,
    required: false,
    default: true,
  },
});

const emits = defineEmits(["update:disabled"]);

let stateBtnEdit = {
  title: "Редактировать",
  localState: "edit",
};

const disabledItems = computed(() => props.disabled);

const startEditTemplate = () => {
  if (stateBtnEdit.localState === "edit") {
    stateBtnEdit.title = "Сохранить";
    stateBtnEdit.localState = "save";
    emits("update:disabled", false);
  } else {
    endEditTemplate();
    stateBtnEdit.title = "Редактировать";
    stateBtnEdit.localState = "edit";
  }
};

const deleteTemplate = (id: number) => {
  templateStore.deleteTemplate(id);
  //TODO(tolstov): записать ответ удаления в переменную и при положительном ответе высветить уведомление
  toast("Шаблон удален!", {
    theme: "auto",
    type: "success",
    position: "top-center",
    transition: "flip",
  });
};

const endEditTemplate = () => {
  const template = {
    id: props.template!.id,
    locationStart: locationStart.value,
    locationEnd: locationEnd.value,
    time: inputTime.time.value,
    name: props.template.name,
    sumPeople: Number(inputPeople.peopleCount.value),
    description: orderDescription.description.value,
    workingHours: inputWorkingTime.time.value,
    workers: {
      value: workers.value,
    },
    priceInfo: {
      price: inputPrice.price.value,
      typeFormat: 1, //добавить тип оплаты - час или сдельно
      typePayment: "Диспетчер", //добавить тип оплаты комиссии
    },
  } as Template;

  templateStore.editTemplate(template);
  emits("update:disabled", true);
};

const orderSwitcherParams = ref([
  {
    id: 1,
    value: "Грузчики",
    isSwitch: false,
  },
  {
    id: 2,
    value: "Грузоперевозки",
    isSwitch: false,
  },
]);

let workers = ref("");

if (props.template) {
  workers.value = props.template.workers.value;
  orderSwitcherParams.value.find((item) => {
    if (item.value === workers.value) {
      item.isSwitch = true;
    }
  });
}

//input text area
const orderDescription = {
  description: ref(""),
  isValid: ref(false),
};

// input text
const locationStart = ref(props.template?.locationStart || "");
const locationEnd = ref(props.template?.locationEnd || "");

// input number
const inputPeople = {
  peopleCount: ref(props.template?.sumPeople || null),
  isValid: ref(false),
};
const inputPrice = {
  price: ref(props.template?.priceInfo.price || null),
  isValid: ref(false),
};

const inputWorkingTime = {
  time: ref(props.template?.workingHours || null),
  isValid: ref(false),
};
const inputTime = {
  time: ref(props.template?.time || ""),
  isValid: ref(false),
};

const orderCheckBoxParams = ref(null);
</script>

